:root {
  --index: calc(1vw + 1vh);
  --transition: ease;
  --white: #FFF;
  --site-white: #FFFFF0;
  --link: rgba(54, 56, 56, 0.9033);
  --gray: #DFDFDF;
  --title: #363838;
  --default: #5A5A5A;
  --accent: #f1d152;
  --dove-gray: #707070;
  --line: #979797;
  --top-bg: #F1D152;
  --line1: #6D7278;
  --partners-fill: rgb(151, 151, 151, .7);
  --bg-play-btn: rgba(54, 56, 56, 0.4);
  --copyright: #A1A1A1;
  --tags-blog: #8F8D8D;
  --works-path-line: rgba(54, 56, 56, 0.7);
  --black: #000000;
  --bs: -2px 3px 17px 0 #000;
  --font-family: "OpenSans", sans-serif;
  --second-family: "Montserrat", sans-serif;
  --text-shadow: 0px 0px 10px rgba(0, 0, 0, 0.5);
  --bg-fill: rgba(255, 255, 255, .6);
  --br: 14px;
  --border-radius: 10px;
  --space-sm: 0.75em;
}

.card__info-link, .card__info-collection, .hero__slider-title {
  font-family: "Montserrat", sans-serif;
  font-weight: 700;
}

.hero {
  padding-bottom: 87px;
}
.hero .swiper-wrapper {
  padding-bottom: 10px;
}
.hero .swiper-slide-active {
  outline: none;
  border: none;
}
.hero__bg {
  position: relative;
}
.hero__bg::after {
  content: "";
  position: absolute;
  background-image: image-set(url("../images/convert/hero/main_slider.avif") type("image/avif"), url("../images/convert/hero/main_slider.webp") type("image/webp"), url("../images/convert/hero/main_slider.jpeg"));
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  width: 100%;
  height: clamp(24.375rem, 0.661rem + 37.05vw, 45.6875rem);
  z-index: -1;
  top: 0;
}
.hero__slider-item {
  display: flex !important;
  align-items: center;
  justify-content: space-evenly;
}
.hero__slider-info {
  max-width: 645px;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.6);
  margin-top: -75px;
}
.hero__slider-title {
  position: relative;
  margin-bottom: 26px;
  padding-bottom: 26px;
  font-size: clamp(1.688rem, 0.65rem + 5.19vw, 6.875rem);
  line-height: 1.2;
  color: var(--title);
  text-wrap: balance;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.6);
  text-align: left;
}
.hero__slider-title::after {
  width: clamp(6.938rem, 5.064rem + 9.37vw, 11.563rem);
  height: 1px;
  background-color: var(--line1);
}
.hero__slider-text {
  max-width: 568px;
  width: clamp(18.125rem, 14.65rem + 17.38vw, 35.5rem);
}
.hero__slider-container {
  margin-top: -16px;
  width: clamp(32.188rem, 8.688rem + 29.38vw, 43.938rem);
  height: clamp(17.5rem, 10.813rem + 33.44vw, 50.938rem);
}
.hero__slider-img {
  object-fit: cover;
}
@media screen and (width <= 1280px) {
  .hero__slider-item {
    gap: 20px;
  }
  .hero__slider-info, .hero__slider-container {
    transform: none;
  }
  .hero__slider-container {
    width: clamp(21.875rem, 4.967rem + 35.23vw, 33.125rem);
    height: auto;
  }
}
@media screen and (width <= 1024px) {
  .hero__bg::after {
    height: clamp(16.625rem, 5.658rem + 29.25vw, 24.375rem);
  }
}
@media screen and (width <= 768px) {
  .hero__bg::after {
    height: 100%;
  }
  .hero__slider-item {
    justify-content: space-around;
    justify-self: unset;
  }
  .hero__slider-info {
    margin-top: 10px;
  }
  .hero__slider-container {
    max-width: 210px;
    width: 100%;
  }
}
@media screen and (width <= 600px) {
  .hero .swiper {
    margin-inline: 10px;
  }
  .hero__slider-item {
    flex-direction: column-reverse;
    justify-content: center;
  }
  .hero__slider-info, .hero__slider-text {
    max-width: none;
    width: 100%;
  }
  .hero__slider-container {
    max-width: none;
    width: 100%;
  }
}

.swiper-pagination {
  position: relative !important;
  display: flex;
  justify-content: center;
  gap: clamp(0.625rem, 0.372rem + 1.27vw, 1.25rem);
  padding-block: 20px;
}

.swiper-pagination-bullet {
  font-size: 0;
  width: 8px;
  height: 8px;
  background: var(--title, #363838);
  box-shadow: var(--bs);
}

.swiper-pagination-bullet.swiper-pagination-bullet-active {
  background: var(--accent, rgba(255, 239, 53, 0.9019607843));
}

.new-collection {
  padding-bottom: 104px;
  text-align: center;
}
.new-collection .subtitle {
  padding-bottom: 25px;
  margin-bottom: 28px;
  line-height: 142%;
}
.new-collection .subtitle::after {
  width: 252px;
  height: 1px;
  margin: 0 auto;
  right: 0;
}
.new-collection__text {
  max-width: 750px;
  margin: 0 auto 53px;
  text-wrap: balance;
}

.collection {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  gap: 30px;
  place-items: center;
}
.collection__item {
  position: relative;
  cursor: pointer;
  perspective: 1000px;
  max-width: 350px;
  height: 350px;
  width: 100%;
}
.collection__slide {
  position: absolute;
  inset: 0;
  backface-visibility: hidden;
  transform-style: preserve-3d;
}
.collection__slide:nth-child(1) {
  animation: slideInOut1 15s infinite ease-in-out;
  z-index: 2;
}
.collection__slide:nth-child(2) {
  animation: slideInOut2 15s infinite ease-in-out;
  z-index: 1;
}
@media screen and (width < 1621px) {
  .collection {
    grid-template-columns: repeat(3, 1fr);
  }
  .collection__img {
    width: 100%;
  }
}
@media screen and (width < 1201px) {
  .collection {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media screen and (width < 769px) {
  .collection {
    grid-template-columns: repeat(1, 1fr);
    place-items: center;
  }
}

@keyframes slideInOut1 {
  0% {
    transform: rotateY(0deg);
    opacity: 1;
  }
  40% {
    transform: rotateY(90deg);
    opacity: 0;
  }
  50% {
    transform: rotateY(90deg);
    opacity: 0;
  }
  90% {
    transform: rotateY(0deg);
    opacity: 1;
  }
  100% {
    transform: rotateY(0deg);
    opacity: 1;
  }
}
@keyframes slideInOut2 {
  0% {
    transform: rotateY(-90deg);
    opacity: 0;
  }
  40% {
    transform: rotateY(0deg);
    opacity: 1;
  }
  50% {
    transform: rotateY(0deg);
    opacity: 1;
  }
  90% {
    transform: rotateY(-90deg);
    opacity: 0;
  }
  100% {
    transform: rotateY(-90deg);
    opacity: 0;
  }
}
.card {
  position: relative;
  overflow: hidden;
  width: 100%;
}
.card--visible {
  overflow: visible;
}
@media (any-hover: hover) {
  .card:hover .card__info {
    transform: translateY(0);
  }
}
.card__link {
  position: relative;
  width: 100%;
  width: clamp(8.125rem, -1.304rem + 19.64vw, 21.875rem);
  height: clamp(8.125rem, -1.304rem + 19.64vw, 21.875rem);
}
.card__title {
  font-weight: 600;
  font-size: 14px;
  line-height: 200%;
  color: var(--dove-gray);
  text-align: center;
}
.card__img {
  object-fit: cover;
  transition-duration: 550ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}
.card__info {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 10px;
  position: absolute;
  height: 100%;
  width: 100%;
  inset: 0;
  background-color: var(--bg-fill);
  transition-duration: 550ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  transform: translateY(calc(100% + 1px));
}
.card__info-link {
  font-size: clamp(0.875rem, 0.8125rem + 0.5vw, 1.125rem);
  line-height: 1.2;
  text-align: center;
  color: var(--title);
  position: relative;
  padding-bottom: 19px;
  margin-bottom: 19px;
}
.card__info-link::before {
  position: absolute;
  content: "";
  width: 80%;
  height: 1px;
  background: var(--line);
  margin: 0 auto;
  left: 0;
  right: 0;
  bottom: 0;
}
.card__info-link {
  transition-duration: 550ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}
@media (any-hover: hover) {
  .card__info-link:hover {
    transform: scale(1.1);
  }
  @supports (-webkit-text-stroke: 1px var(--accent, rgba(255, 239, 53, 0.9019607843))) {
    .card__info-link:hover {
      -webkit-text-stroke: 1px var(--accent, rgba(255, 239, 53, 0.9019607843));
    }
  }
  @supports not (-webkit-text-stroke: 1px var(--accent, rgba(255, 239, 53, 0.9019607843))) {
    .card__info-link:hover {
      text-shadow: -1px -1px 0 var(--accent, rgba(255, 239, 53, 0.9019607843)), 1px -1px 0 var(--accent, rgba(255, 239, 53, 0.9019607843)), -1px 1px 0 var(--accent, rgba(255, 239, 53, 0.9019607843)), 1px 1px 0 var(--accent, rgba(255, 239, 53, 0.9019607843));
    }
  }
}
.card__info-collection {
  font-size: clamp(0.875rem, 0.8125rem + 0.5vw, 1.125rem);
  line-height: 1.2;
  text-align: center;
  color: var(--title);
  position: relative;
  padding-bottom: 19px;
  margin-bottom: 19px;
}
.card__info-collection::before {
  position: absolute;
  content: "";
  width: 80%;
  height: 1px;
  background: var(--line);
  margin: 0 auto;
  left: 0;
  right: 0;
  bottom: 0;
}

.cta {
  position: relative;
  background-image: image-set(url("../images/convert/cta/img1@1x.avif") type("image/avif") 1x, url("../images/convert/cta/img1@2x.avif") type("image/avif") 2x, url("../images/convert/cta/img1@1x.webp") type("image/webp") 1x, url("../images/convert/cta/img1@2x.webp") type("image/webp") 2x, url("../images/convert/cta/img1@1x.jpg") type("image/jpeg") 1x);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  min-height: 616px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  box-shadow: 0 8px 24px rgba(0, 0, 0, 0.2);
  overflow: hidden;
  z-index: 0;
}
.cta__content {
  position: relative;
  z-index: 2;
  text-align: left;
}
.cta__title {
  width: 100%;
  max-width: 730px;
  padding-bottom: 25px;
  margin-bottom: 22px;
  font-size: clamp(1.875rem, 1.5rem + 1.88vw, 3.75rem);
  line-height: 133%;
  text-align: left;
