@media screen and (width < 881px) {
  .cta {
    padding: 70px 0;
    min-height: 100%;
  }
}

.background-layer {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: var(--bg-fill);
  z-index: 1;
  pointer-events: none;
  transform: translateX(-100%);
  transition: transform 1.2s ease-out;
  animation-delay: 0.8s;
}
.background-layer.animate {
  transform: translateX(0);
}

.chevron {
  position: absolute;
  bottom: -20%;
  left: 50%;
  transform: translateX(-50%);
  animation: arrowDown 2s infinite;
}
.chevron--bottom {
  opacity: 0;
  animation-delay: 1s;
}

@keyframes arrowDown {
  0% {
    bottom: -20% px;
    opacity: 1;
  }
  100% {
    bottom: -30%;
    opacity: 0;
  }
}
.works-path {
  padding: 80px 20px 30px;
  position: relative;
  background: white;
  box-shadow: inset 0 0 10px 5px rgba(0, 0, 0, 0.3);
}
.works-path:before, .works-path:after {
  z-index: -1;
  position: absolute;
  content: "";
  bottom: 0;
  left: 10px;
  width: 50%;
  top: 80%;
  max-width: 900px;
  background: #777;
  box-shadow: 0 15px 10px #777;
  transform: rotate(-3deg);
}
.works-path:after {
  transform: rotate(3deg);
  right: 10px;
  left: auto;
}
.works-path__title {
  margin-bottom: 40px;
  line-height: 142%;
}
.works-path .subtitle::after {
  display: none;
}
.works-path__items-box {
  display: flex;
  align-items: flex-end;
  border-bottom: 2px solid var(--works-path-line);
  justify-content: space-between;
  gap: 50px;
}
.works-path__item {
  width: 30%;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}
.works-path__item--designer {
  position: relative;
}
.works-path__item--designer::after {
  position: absolute;
  content: "";
  width: 10px;
  height: 10px;
  background-color: var(--title);
  border-radius: 50%;
  bottom: -5px;
  left: 0;
  right: 0;
}
.works-path__item--production::after, .works-path__item--measurements::before {
  position: absolute;
  content: "";
  width: 20px;
  height: 2px;
  background-color: var(--works-path-line);
  transform: rotate(90deg);
}
.works-path__item--production {
  position: relative;
  padding-bottom: 35px;
}
.works-path__item--production::after {
  bottom: 8px;
  left: -7px;
}
.works-path__item--measurements {
  position: relative;
  margin-inline: auto;
  margin-top: 10px;
}
.works-path__item--measurements::before {
  top: -2px;
  left: -12px;
}
.works-path__top {
  display: flex;
  align-items: center;
  gap: 13px;
}
.works-path-inner {
  display: flex;
  align-items: center;
}
.works-path__text {
  margin-top: 13px;
}
@media screen and (width < 1025px) {
  .works-path__item {
    width: 350px;
  }
}
@media screen and (width < 641px) {
  .works-path {
    padding-inline: 0;
  }
  .works-path__items {
    width: 90%;
    margin-inline: auto;
  }
  .works-path__items-box {
    border-bottom: none;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
  }
  .works-path__item {
    width: 100%;
  }
  .works-path__item--designer, .works-path__item--production, .works-path__item--measurements {
    margin: 0;
  }
  .works-path__item--production {
    padding-bottom: 0;
    order: 1;
  }
  .works-path__item--designer::after, .works-path__item--production::after, .works-path__item--measurements::before {
    display: none;
  }
}

.blog {
  padding-top: 57px;
  padding-bottom: 102px;
}
.blog__inner {
  position: relative;
  display: flex;
  justify-content: space-between;
  gap: 30px;
}
.blog__items {
  width: 60%;
}
.blog__items--one {
  width: 100%;
}
.blog__blockquote {
  position: relative;
  margin-bottom: 50px;
  perspective: 1000px;
  min-height: 215px;
}
.blog__blockquote-front, .blog__blockquote-back {
  position: absolute;
  width: 100%;
  height: 100%;
  inset: 0;
  transition: 1s;
  backface-visibility: hidden;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 20px;
  text-wrap: balance;
  box-shadow: var(--bs);
  overflow: hidden;
}
.blog__blockquote-front {
  background: rgba(223, 223, 223, 0.5);
}
.blog__blockquote-back {
  background: rgba(223, 223, 223, 0.2);
  transform: rotateY(180deg);
}
.blog__blockquote-text {
  position: relative;
  padding: 5px;
  font-style: italic;
  font-weight: 300;
  font-size: 24px;
  line-height: 158%;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
}
.blog__blockquote-author {
  position: relative;
  padding-top: 10px;
  font-family: var(--second-family);
  font-weight: 700;
  display: block;
  line-height: 144%;
  text-align: center;
}
.blog__blockquote-author::after {
  position: absolute;
  content: "";
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  background-color: var(--line1);
  width: 177px;
  height: 1px;
}
@media (any-hover: hover) {
  .blog__blockquote:hover .blog__blockquote-front {
    transform: rotateY(180deg);
  }
  .blog__blockquote:hover .blog__blockquote-back {
    transform: rotateY(360deg);
  }
}
@media screen and (width < 1041px) {
  .blog__items {
    width: 100%;
  }
  .blog__item {
    max-width: 100%;
  }
}
@media screen and (width < 541px) {
  .blog__blockquote {
    padding: 24px 34px;
  }
  .blog__blockquote-text {
    font-size: 16px;
  }
}

.blog-card {
