.blog-card__title {
  margin-bottom: 10px;
}
.blog-card__video-box {
  position: relative;
  margin-bottom: 17px;
  height: 415px;
}
.blog-card__video {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.blog-card__play-button {
  position: absolute;
  top: 50%;
  left: 50%;
