@media (any-hover: hover) {
  .blog-card__play-button:hover {
    background-color: var(--accent-hover, #ffef35);
    transform: translate(-50%, -50%) scale(1.1);
  }
  .blog-card__play-button:hover::after {
    background-color: var(--bg-play-btn);
    transform: scale(1.1);
