.cards-spring {
  padding-block: clamp(3.25rem, 1.193rem + 4.29vw, 6.25rem) clamp(3.25rem, 1.193rem + 4.29vw, 6.25rem);
}
@media screen and (width <= 558px) {
  .cards-spring {
