.spring {
  display: flex;
  justify-content: center;
}
@media (any-hover: hover) {
  .spring:hover .spring__item {
    transform: rotate(0);
    margin-inline: 10px;
    opacity: 1;
  }
  .spring:hover .card__link:hover .card__img {
    transform: scale(1.2);
  }
}
.spring__item {
  position: relative;
  width: clamp(8.125rem, -1.304rem + 19.64vw, 21.875rem);
  height: clamp(8.125rem, -1.304rem + 19.64vw, 21.875rem);
  box-shadow: var(--bs);
  border-radius: var(--br);
  backdrop-filter: blur(10px);
  margin-inline: -95px;
  transform: rotate(calc(var(--r) * 1deg));
  opacity: 0.7;
  transition-duration: 550ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}
.spring .card {
  border-radius: inherit;
}
.spring .card__img {
